import React from 'react';

import {
  Button,
  Heading,
  Checkbox,
  Stack,
  Input,
  Image,
} from "@chakra-ui/react";

import './Popup.css';


const Popup = () => {

  function onSubmitClick(comments, tags) {
    console.log("submit");
    console.log(comments);
    console.log(tags);
    // window.close();
  }

  const [comments, setComments] = React.useState('');
  const [tags, setTags]         = React.useState('');
  const handleCommentsChange    = (event) => setComments(event.target.value)
  const handleTagsChange        = (event) => setTags(event.target.value)
  
  return (
    <div className="App">
      <header className="App-header">

        <Heading
          fontWeight    = {600}
          fontSize      = {{ base: '2xl', sm: '4xl', md: '6xl' }}
          lineHeight    = {'110%'}
          paddingBottom = {4}
          color         = {'#282c34'}
        >
          ArXiv Enhanced
        </Heading>

        <Stack spacing = {4}>
          <Input type = "text" color = {'#282c34'} placeholder = "Comments" value = {comments} onChange = {handleCommentsChange} />
          <Input type = "text" color = {'#282c34'} placeholder = "Tags"     value = {tags}     onChange = {handleTagsChange} />

          <Checkbox color = {'#282c34'} >
            Completed Reading
          </Checkbox>

          <Button 
            w       = "full" 
            color   = "gray.900"
            onClick = {() => onSubmitClick(comments, tags)}
          >
            Submit
          </Button>
        </Stack>

      </header>
    </div>
  );
};

export default Popup;
